import './FabricanteFeriado.css'
import 'react-date-range/dist/styles.css'
import 'react-date-range/dist/theme/default.css'

import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import { DateRange } from 'react-date-range'
//@ts-ignore
import * as locales from 'react-date-range/dist/locale'
import * as FIcon from 'react-feather'
import InputMask from 'react-input-mask'
import { RouteComponentProps, useRouteMatch } from 'react-router'

import { AdminItemFieldRegistro, FooterFormulario, Page } from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { FORMATO_FECHA_BACKEND, RUTAS_APP_FABRICANTE } from '../../constants'
import { useApiToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import HorarioAtencion from '../../models/types/HorarioAtencion'
import { Feriado as FeriadoApi } from '../../services/api'
import moment from 'moment'

const ErrorDefault = { fecha_inicio: '', fecha_fin: '', hora_cierre: '', nombre: '' }

export const FabricanteFeriado: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const [Loading, setLoading] = useState(false)
  const [Editar, setEditar] = useState(false)
  const match = useRouteMatch()

  const [Feriado, setFeriado] = useState<FeriadoApi>({
    fecha_inicio: '',
    fecha_fin: '',
    nombre: '',
  })

  const [Errors, setErrors] = useState({ fecha_inicio: '', fecha_fin: '', hora_cierre: '', nombre: '' })
  const [Intervalo, setIntervalo] = useState([
    {
      startDate: new Date(),
      endDate: new Date(),
      key: 'selection',
    },
  ])
  const apiToast = useApiToaster()

  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  React.useEffect(() => {
    if (!!Intervalo) {
      const inicio = moment(Intervalo[0].startDate.toISOString()).format('ddd D MMM')
      const fin = moment(Intervalo[0].endDate.toISOString()).format('ddd D MMM')
      console.log(inicio, fin)
      const feriado = { ...Feriado }
      feriado['fecha_fin'] = fin
      feriado['fecha_inicio'] = inicio
      setFeriado(feriado)
    }
  }, [Intervalo[0].startDate, Intervalo[0].endDate])

  React.useEffect(() => {
    //@ts-ignore
    if (!!match.params.id) {
      setEditar(true)
      //@ts-ignore
      obtenerFeriado(match.params.id)
    } else {
      setEditar(false)
    }
  }, [match])

  const setHorario = (campo: string, dato: any) => {
    const horario = { ...HorarioAtencion }
    //@ts-ignore
    horario[campo] = dato
    //@ts-ignore
    setHorarioInput(horario)
  }

  const setDato = (campo: string, valor: any) => {
    let datos = { ...Feriado }
    // @ts-ignore
    datos[campo] = valor
    setFeriado(datos)
  }

  const obtenerFeriado = (id: number) => {
    setLoading(true)
    rootStore.environment.api.adminGetFeriado(id).then((result) => {
      if (result.kind === 'ok') {
        const data = result.data as any
        setFeriado(data)
        const inicio = moment(data.fecha_inicio, FORMATO_FECHA_BACKEND)
        const fin = moment(data.fecha_fin, FORMATO_FECHA_BACKEND)
        setIntervalo([
          {
            startDate: new Date(inicio.format()),
            endDate: new Date(fin.format()),
            key: 'selection',
          },
        ])
      }
      setLoading(false)
    })
  }

  const guardarFeriado = () => {
    const datos = { ...Feriado }
    datos.fecha_inicio = moment(Intervalo[0].startDate).format(FORMATO_FECHA_BACKEND)
    datos.fecha_fin = moment(Intervalo[0].endDate).format(FORMATO_FECHA_BACKEND)

    setErrors(ErrorDefault)
    setLoading(true)
    rootStore.environment.api.adminGuardarFeriado(datos).then((result) => {
      setLoading(false)
      apiToast(result, 'Feriado guardado exitosamente', 'guardar')
      if (result.kind === 'ok') {
        document.location.href = RUTAS_APP_FABRICANTE.HORARIO_ATENCION
      } else if (result.kind === 'bad-data') {
        //@ts-ignore
        setErrors(result.errors)
      }
    })
  }

  const eliminarFeriado = (id: number) => {
    if (!window.confirm('Confirma que desea eliminar?')) {
      return
    }
    rootStore.environment.api.adminDeleteFeriado(id).then((result) => {
      apiToast(result, 'Feriado eliminado exitosamente', 'eliminar')
      document.location.href = RUTAS_APP_FABRICANTE.HORARIO_ATENCION
    })
  }

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'feriado-fabricante'}>
      <SubHeader
        links={[
          { titulo: 'Horario de atención', ruta: RUTAS_APP_FABRICANTE.HORARIO_ATENCION },
          { titulo: Editar ? 'Editar cierre por feriado' : 'Añadir cierre por feriado' },
        ]}
      />
      <div className="container-sm pt-3">
        <div className="row justify-content-between">
          <div className={Movil ? 'col-12' : 'col-10'}>
            <h2> Cierre por feriado</h2>
            <p>Podés programar por adelantado los días de cierre por feriados que tenga tu tienda.</p>
          </div>
          {!Movil && (
            <div className="col-2">
              {Editar && (
                <a onClick={() => eliminarFeriado(Feriado.id as any)}>
                  <FIcon.Trash color="var(--gris-oscuro)" />
                </a>
              )}
            </div>
          )}
        </div>

        {Loading && (
          <div className="h-100 d-flex justify-content-center align-content-center">
            <IonSpinner />
          </div>
        )}
        {!Loading && (
          <>
            <div className="selector-fecha-container">
              <div style={{ width: 300 }}>
                <DateRange
                  editableDateInputs={true}
                  moveRangeOnFirstSelection={false}
                  onChange={(item) => {
                    //@ts-ignore
                    setIntervalo([item.selection])
                  }}
                  //@ts-ignore
                  ranges={Intervalo}
                  locale={locales['es']}
                  color="var(--azul-fabricante)"
                />
              </div>

              <div className="d-flex flex-column" style={Movil ? { borderRadius: 'none', flex: 1 } : {}}>
                <div className="header" style={Movil ? { borderRadius: 'none', flex: 1 } : {}}>
                  <div>
                    <span>Motivo</span>
                  </div>
                </div>
                <div className="row h-100 align-items-center px-5" style={Movil ? { flex: 1 } : { width: 400 }}>
                  <AdminItemFieldRegistro
                    deshabilitado={!Loading}
                    titulo={'Motivo'}
                    titulo_externo=""
                    tipo={'texto'}
                    campo={'nombre'}
                    valor={Feriado.nombre ? Feriado.nombre : ''}
                    error={Errors && Errors.nombre}
                    set_dato={setDato}
                    columnas={12}
                    columnas_sm={12}
                  />
                </div>
              </div>
            </div>
          </>
        )}
      </div>
      {!Loading && (
        <FooterFormulario>
          <div className="container-sm py-2">
            <div className="row">
              <div className={Movil ? 'col-12 py-1' : ''}>
                <button className="btn btn-primary w-100 px-4" onClick={guardarFeriado}>
                  Guardar {Editar && 'cambios'}
                </button>
              </div>
              {Movil && Editar && (
                <div className={'col-12 py-1'}>
                  <button className="btn btn-primary w-100" onClick={guardarFeriado}>
                    Eliminar
                  </button>
                </div>
              )}
            </div>
          </div>
        </FooterFormulario>
      )}
    </Page>
  )
})

export default FabricanteFeriado
